import { StudentService } from './../../services/student.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit {

  student:any={
  _id:'',
  name:'',
  email:'',
  password:'',
  phone:'',
  faculty:'',
  department:''
  }


  facultyData:any;
  departments:any;
  studentData:any;
  constructor(private studentService:StudentService) {}

  ngOnInit() {
    document.getElementById('form1').style.display="none";
    this.studentService.getFacultyData().subscribe(
      res=>  this.facultyData=res);
    this.studentService.getStudentsData().subscribe(
      res=>this.studentData=res
    );
  }
  display(name)
  {
    const state =document.getElementById(name).style.display;
    if(state=="none") document.getElementById(name).style.display="inline";
    else document.getElementById(name).style.display="none";
  }
  selectFacultyHandler (event: any) {

     const number = event.target.value;
     this.departments=this.facultyData[number].departments;
     this.student.faculty=this.facultyData[number].facultyName;
  }
  selectDeptHandler(event:any)
  {
    const number = event.target.value;
    this.student.department=this.departments[number];
  }


  onSubmit({form1,valid})
  {
    if(!valid||this.student.department==''||this.student.faculty=='')
    return alert('Please Complete the fields');
    this.studentService.register(this.student).subscribe(res=>{
      alert('DONE');
      this.studentService.getStudentsData().subscribe(res=>this.studentData=res);
      this.student.faculty='';
      this.student.department='';
      document.getElementById("reset").click();
    })

  }

  Fill(number)
  {
    this.student=this.studentData[number];
    document.getElementById('form1').style.display="inline";
    console.log(this.student._id);
  }

  delete(index)
  {
    const id = this.studentData[index]._id;
    this.studentService.deleteStudent(id).subscribe(res=>{
     this.studentService.getStudentsData().subscribe(res=>this.studentData=res);
    });
  }

}
