import { UserService } from 'src/app/services/user.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  curUser:any = this.user.getUserData();
  editUser:any = this.user.getUserData();
  check: any = {
    filter: '',
    data: ''
  };
  foundUser:any;
  foundEmail:any;

  constructor(private user:UserService,private router:Router) { }
  ngOnInit() {
    if(!this.user.isLoggedIn())
     {
       alert('You must be logged in!!');
       this.router.navigate(['login']);
     }
  }

  onSubmit({form1,valid})
  {
      if(!valid) return alert('Complete the fields');
      if(this.foundUser||this.foundEmail) return alert('Read the errors');
      this.user.edit(this.editUser).subscribe(res=>{
          if(!res.success)
          return alert('Error occurred ,try again please');
          alert('Editing has been done ! You have to login again ');
          this.user.logOut();
          this.router.navigate(['login']);
      });
  }

  checkExist(filter) {
    if(this.editUser.username==this.curUser.username&&filter=='username')
     {
       this.foundUser=0;
       return;
     }
      if(this.editUser.email==this.curUser.email&&filter=='email')
     {
       this.foundEmail=0;
       return;
     }

    this.check.filter = filter;
    if(filter=='username')
    this.check.data = this.editUser.username;
    else this.check.data=this.editUser.email;

    this.user.check(this.check).subscribe(res => {
     if(filter=='username') this.foundUser=res.found;
     else this.foundEmail=res.found;
    });
  }

}
