import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  User:any= this.user.getUserData();
  oldPassword:any;
  newPassword:any;
  compared:any;
  constructor(private user:UserService,private router:Router) { }

  ngOnInit() {

    if(!this.user.isLoggedIn())
     {
       alert('Please Login to Access this page');
       this.router.navigate(['login']);
     }
     console.log(this.User);

     }

    logout()
    {
      this.user.logOut();
      this.router.navigate(['login']);
    }

    check()
    {
      if(this.User.password==this.oldPassword) return this.compared=0;
      this.compared=1;
    }
    changePassword({form1,valid})
    {
       if(!valid) return alert('Complete the fields');
       if(this.oldPassword!=this.User.password) return alert('Complete the fields');
       this.user.changePassword({id:this.User._id,newPassword:this.newPassword}).subscribe(
         res=>{

          if(res.success=='error') return alert('ERROR');
          alert('Done ,you have to login again');
          this.user.logOut();
          this.router.navigate(['/login']);
         }
       );
    
    }

}
